import * as React from "react";
import * as styles from "./heroCarousel.module.css";
import { StaticImage } from "gatsby-plugin-image";

export default function HeroCarousel() {
  const items = [
    {
      id: 1,
      fileName: "Lane.png",
      label: "",
      width: 70,
    },
    {
      id: 2,
      fileName: "Seltzer.png",
      label: "",
      width: 70,
    },
    {
      id: 3,
      fileName: "Sippy.png",
      label: "",
      width: 70,
    },
    {
      id: 4,
      fileName: "SnowLeopard.png",
      label: "",
      width: 70,
    },
    {
      id: 5,
      fileName: "VIVA_Cider.png",
      label: "",
      width: 150,
    },
  ];
  const widt = 300;
  const heig = 400;
  return (
    <div
      className={`${styles.sliderImgContainer} ${styles.sliderImgContainer_hover_pause}`}
    >
      <div className={styles.slider_group}>
        <span>
          <StaticImage
            alt={"Lane"}
            src={`../../images/products/Lane.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"Seltzer"}
            src={`../../images/products/Seltzer.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"Sippy"}
            src={`../../images/products/Sippy.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"SnowLeopard"}
            src={`../../images/products/SnowLeopard.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"VIVA_Cider"}
            src={`../../images/products/VIVA_Cider.png`}
            width={widt + widt}
            height={heig}
          />
        </span>
      </div>

      <div className={styles.slider_group} aria-hidden="true">
        <span>
          <StaticImage
            alt={"Lane"}
            src={`../../images/products/Lane.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"Seltzer"}
            src={`../../images/products/Seltzer.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"Sippy"}
            src={`../../images/products/Sippy.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"SnowLeopard"}
            src={`../../images/products/SnowLeopard.png`}
            width={widt}
            height={heig}
          />
        </span>
        <span>
          <StaticImage
            alt={"VIVA_Cider"}
            src={`../../images/products/VIVA_Cider.png`}
            width={widt + widt}
            height={heig}
          />
        </span>
      </div>
    </div>
  );
}
